*{
    margin: 0;
    padding:0;
    box-sizing: border-box;
    text-decoration: none;
}
.dp{
    background-color: var(--background);
    padding:0.5rem;
}
#root div h1{
    text-align: center;
    padding:0.75rem 0;
    color: var(--text);
}
video{
    display: block;
    margin:0.5rem auto;
    width: 80%;
}
#root div p{
    text-align: center;
}
@media screen and (max-width: 480px){
    video{
        width: 95%;
        margin: 1rem auto;
    }
}